<template>
  <div class="wrapper">

    <h3 class="title py-4">Добавить продукт</h3>

    <v-container class="pa-0" grid-list-md>
      <v-form>
        <v-flex >
          <v-layout row wrap>
            <v-flex lg12>
              <v-text-field
                v-model="title"
                label="Название"
                class="mb-3"
                solo
                required
              ></v-text-field>
            </v-flex>
            <v-flex lg6>
              <v-text-field
                v-model="price"
                label="Цена"
                class="mb-3"
                solo
                required
              ></v-text-field>
            </v-flex>
            <v-flex lg6>
              <v-select
                :items="category"
                label="Solo field"
                class="mb-3"
                solo
              ></v-select>
            </v-flex>
            <v-flex>
              <v-text-field
                v-model="shopLocation"
                label="Адрес магазина"
                class="mb-3"
                solo
                required
              ></v-text-field>
            </v-flex>
            <v-flex>
              <v-text-field
                v-model="shopName"
                label="Название магазина"
                class="mb-3"
                solo
                required
              ></v-text-field>
            </v-flex>
          </v-layout>
        </v-flex>
      </v-form>
    </v-container>
  </div>
</template>

<script>

export default {
  name: 'add-product',
  data() {
    return {
      title: '',
      price: '',
      category: [
        'Напитки', 'Категория 2', 'Категория 3',
      ],
      shopLocation: '',
      shopName: '',
    };
  },
};
</script>

<style scoped>
.wrapper {
  width: 100%;
  max-width: 720px;
  margin: 0 auto;
}


</style>



